header {
    margin-top: 5rem;
}
main {
    width: 100%;
    
}

.main-body{
    margin: 127px auto 0 auto;
}


p {
    color: #2e2e2e;
    font-size: 2rem;
    line-height: 1.2;
    
}


.entry-title {
    background-color: #f3de49;
    padding: 0 1rem; 
    display: inline-block;
   
}

.entry-title a {
    text-decoration: none;
    color: #2e2e2e!important;
   
}

.rounded {
    height: 575px;
    width: 100%;
    background-image: url("http://wordpressalgo.test/wp-content/uploads/2019/11/taylor-vick-M5tzZtFCOfs-unsplash.jpg");
    background-repeat: no-repeat;
}

.lead-image{
    background-image: url("http://wordpressalgo.test/wp-content/uploads/2019/10/finalimage.png");
    background-repeat: no-repeat;
    width: 450px;
    height: 251px;
    

}

.text {
    width: 80%;
     margin: auto;
    font-size: 6.2rem;
    color: #343434;
    display: flex;
    justify-content: space-between;
    padding-top: 9rem;
    align-items: center;
    align-content: center;
    h2{
        font-weight: 300;
        width: 50%;
        font-family: 'Cabin Sketch', cursive;
        margin-top: 4rem;
        color: #cbcbcb;

    }
    
}


.container-intro{
    width: 80%;
    margin: auto;
    padding: 5rem 0;
    
    .intro {font-family: 'Cabin Sketch', cursive;
        font-size: 2.8rem;
        border-top: 1px solid #cbcbcb;
        padding-top: 4rem;
    }
}

.container-articles{
    width: 80%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    margin: auto;
 

.home-article {
   
    width: 45%;
    margin-bottom: 6rem;
    p:first-of-type {
        font-size: 3rem;
        margin: 2.4rem 0 1rem 0;
        color: #2e2e2e;
    }
    p{
        line-height: 1.25;
        font-size: 2.15rem;
        color: rgba(0, 0, 0, 0.605);
        color: rgba(0, 0, 0, 0.605);
        margin-top: 7px;
    }
}


